@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);
html {
  /* border-box box model allows us to add padding and border to our elements without increasing their size */
  box-sizing: border-box;
  /* A system font stack so things load nice and quick! */
  font-family: 'Open Sans', Helvetica, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: white;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
   background: -webkit-linear-gradient(bottom, rgba(40,44,52,0.8) 20%, rgba(16,29,55,0.90) 40%), -webkit-linear-gradient(bottom, rgba(246,1,1,0.76) 10%, rgba(224,30,30,0.1) 40%), url(/joeljames/static/media/collab.254efe2a.jpg) center;
   background: linear-gradient(0deg, rgba(40,44,52,0.8) 20%, rgba(16,29,55,0.90) 40%), linear-gradient(0deg, rgba(246,1,1,0.76) 10%, rgba(224,30,30,0.1) 40%), url(/joeljames/static/media/collab.254efe2a.jpg) center;
  background-size: cover;
  min-height: calc(100vh - 100px);
  /* margin: 50px; */
  background-attachment: fixed;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 5px 0;
}

div {
    border: 0px;
    display: flex;
    flex-direction: column;
    /* border: 3px solid orange; */
}

div.land-header {
    height: 100px;
    justify-content: flex-end;
    /* transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1); */
}

div.land-header h3 {
    align-self: center;
    font-size: 2em;
    font-weight: 100;
    letter-spacing: 2px;
    color: white;
    text-transform: uppercase;
    /* transition: 2s cubic-bezier(0.075, 0.82, 0.165, 1); */
}

div.land-title {
    height: 300px;
    justify-content: flex-end;
    align-items: center;
    /* animation-delay: 1s;
    transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1); */
}

div.land-title h1 {
    /* border: 2px solid blue; */
    color: #d63031;
    font-family: 'Abril Fatface', cursive;
    font-weight: 500;
    font-size: 5em;
    letter-spacing: 2px;
}

button.bring-up-nav {
    align-self: flex-end;
    background: transparent;
    border: none;
}

p.location {
    align-self: center;
    color: white;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding-top: 100px;
    height: 200px;
    /* border: 2px solid white; */
}

.bring-up-nav {
    /* border: 2px solid orange; */
    flex-direction: row;
    align-content: center;
    justify-content: center;
    height: 100px;
}

.slide-right {
    position: absolute;
    height: 70px;
    width: 70px;
    z-index: 2;
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
}

.slide-right:hover {
    position: absolute;
    background-color: rgba(224,30,30,0.2);
    height: 70px;
    width: 70px;
    z-index: 0;
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
    -webkit-transition: 1s;
    transition: 1s;
    cursor: pointer;
}

i.fa.fa-angle-double-right {
    position: absolute;
    height: 70px;
    width: 70px;
    /* z-index: 2; */
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
}
/* Imported CSS from: Rıza Selçuk Saydam - https://codepen.io/rss/pen/vIDKH */

#nav-container{
    align-self: flex-end;
    /* border: 2px solid white; */
}

.toggle-icon {
	display: block;
	cursor: pointer;
}

.toggle-icon .bar {
	display: block;
	width: 3.8em;
	height: 0.2em;
    margin: 0.9em;
	border-right: 2.8em solid #d63031;
	border-left: 0.6em solid #d63031;
	box-sizing: border-box;
}
/* End imported CSS */
div.nav {
    /* border: 2px solid white; */
    position: absolute;
    left: 100%;
    top: 0;
    /* left: 0; */
    display: flex;
    flex-direction: row;
    background-color: rgba(246,1,1,0.70);
    height: 100%;
    width: 100%;
    display: none;
    color: white;
    font-family: 'Abril Fatface', cursive;
    /* transition from right, cubic bezier, animation slide left */
}

div.nav h1 {
    /* border: 2px solid blue; */
    margin: auto;
    font-size: 5em;
    letter-spacing: 6px;

}

div.nav.toggle {
        position: absolute;
        top: 0px;
        left: 0px;
        background-color: #d63031;
        height: 100%;
        width: 100%;
        z-index: 100;
}


